@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.navbar {
  background: transparent;
  height: 80px;
  transition: all .3s;
}

.navbarActive {
  background: white;
  height: 60px;
  filter: drop-shadow(5px 0 19px rgb(0, 0, 0, .2));
  transition: all .3s;
}

.logo {
  width: 200px;
  height: 100%;
  transition: all .5s;
}

.logoActive {
  width: 150px;
  height: 100%;
  transition: all .3s;
}

.link {
  font-weight: 900;
  padding: 10px;
  margin: 0 10px;
  color: #2e2e2e;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.link:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: rgb(87, 87, 87);
  transition: left .8s;
}

.link:hover:after {
  left: 0;
}

.active {
  background: rgb(206, 206, 206);
  color: white;
  transition: all .5s;
}

.icono {
  color: white;
  font-size: 17px;
  margin-right: 10px;
}

