.slick-slide img {
  margin: auto;
}

.slick-prev{
  left: 0;
}

.slick-next{
  right: 22px;
}

.slick-prev, .slick-next {
  z-index: 3;
  position: absolute;
}

.slick-prev:before {
  left: 0;

}

.slick-next:before, .slick-prev:before {
  font-size: 40px;
  color: white;
  z-index: 1;
}

